<template>
  <div class="card">
    <img
      src="../assets/illustration-thank-you.svg"
      alt="illustration-thank-you"
      v-if="showMessage"
      class="message-img"
    />
    <div class="star-container" v-else>
      <img src="../assets/icon-star.svg" alt="icon-star" />
    </div>
    <p class="selected-rating-text" v-if="showMessage">
      You selected {{ selectedRating }} out of 5
    </p>
    <h1 class="heading" :style="showMessage && 'text-align: center'">
      {{ showMessage ? "Thank you!" : "How did we do?" }}
    </h1>
    <p class="subtext" :style="showMessage && 'text-align: center'">
      {{
        showMessage
          ? "We appreciate you taking the time to give a rating. If you ever need more support, don't hesitate to get in touch!"
          : "Please let us know how we did with your support request. All feedback is appreciated to help us improve our offering!"
      }}
    </p>
    <div class="rating-list" v-if="!showMessage">
      <div
        v-for="(item, index) in ratingList"
        :key="index"
        @click="selectedRating = item"
        :class="['rating-list-item', selectedRating === item && 'active']"
      >
        {{ item }}
      </div>
    </div>
    <button
      :class="['submit-btn', !selectedRating && 'disabled']"
      v-if="!showMessage"
      @click="showMessage = true"
    >
      submit
    </button>
  </div>
</template>

<script>
export default {
  name: "RatingCard",
  data() {
    return {
      selectedRating: null,
      ratingList: [1, 2, 3, 4, 5],
      showMessage: false,
    };
  },
  methods: {},
};
</script>
